"use client";
import { useState } from "react";

export default function CreatePollForm({ sessionCode, onClose }: { sessionCode: string; onClose: () => void }) {
  const [question, setQuestion] = useState("");
  // Added "open_ended" to the state type
  const [type, setType] = useState<"multiple_choice" | "rating" | "word_cloud" | "open_ended">("multiple_choice");
  const [options, setOptions] = useState(["", ""]);
  const [loading, setLoading] = useState(false);

  const handleSubmit = async () => {
    if (!question) return;

    // Payload construction
    const payload: any = { question, type };
    if (type === "multiple_choice") {
       payload.options = options.filter(o => o.trim() !== "");
       if (payload.options.length < 2) return alert("Add at least 2 options");
    }

    setLoading(true);
    try {
        await fetch(`http://localhost:8001/api/session/${sessionCode}/poll/start`, {
            method: "POST",
            headers: { "Content-Type": "application/json" },
            body: JSON.stringify(payload)
        });
        onClose();
    } catch (e) {
        alert("Failed to start poll");
        setLoading(false);
    }
  };

  return (
    <div className="fixed inset-0 bg-black/80 flex items-center justify-center p-4 z-50 backdrop-blur-sm">
      <div className="bg-slate-900 border border-slate-700 p-6 rounded-2xl w-full max-w-md shadow-2xl">
        <h2 className="text-xl font-bold mb-4 text-white">Launch Widget</h2>

        {/* Type Selector Grid */}
        <div className="grid grid-cols-2 sm:grid-cols-4 gap-2 mb-4">
            {[
                { id: "multiple_choice", label: "üìä Poll" },
                { id: "rating", label: "‚≠ê Rating" },
                { id: "word_cloud", label: "‚òÅÔ∏è Words" },
                { id: "open_ended", label: "üí≠ Thoughts" } // <--- New Option
            ].map((t) => (
                <button
                    key={t.id}
                    onClick={() => setType(t.id as any)}
                    className={`py-2 text-sm rounded-lg border font-medium transition-all ${
                        type === t.id 
                        ? "bg-blue-600 border-blue-500 text-white shadow-lg shadow-blue-900/50" 
                        : "bg-slate-800 border-slate-700 text-slate-400 hover:bg-slate-700 hover:text-white"
                    }`}
                >
                    {t.label}
                </button>
            ))}
        </div>
        
        <input 
          autoFocus
          className="w-full bg-slate-800 border border-slate-700 p-3 rounded-lg mb-4 text-white placeholder-slate-500 focus:ring-2 focus:ring-blue-500 outline-none transition"
          placeholder="Ask a question..."
          value={question}
          onChange={(e) => setQuestion(e.target.value)}
          onKeyDown={(e) => e.key === 'Enter' && type !== 'multiple_choice' && handleSubmit()}
        />

        {/* Dynamic Inputs based on Type */}
        {type === "multiple_choice" && (
          <div className="space-y-2 mb-4 max-h-50 overflow-y-auto pr-1 custom-scrollbar">
            {options.map((opt, idx) => (
               <input
                 key={idx}
                 className="w-full bg-slate-800/50 border border-slate-700 p-2 rounded text-sm text-white focus:border-blue-500 outline-none"
                 placeholder={`Option ${idx + 1}`}
                 value={opt}
                 onChange={(e) => {
                    const newOpts = [...options];
                    newOpts[idx] = e.target.value;
                    setOptions(newOpts);
                 }}
               />
            ))}
            <button onClick={() => setOptions([...options, ""])} className="text-xs text-blue-400 hover:text-blue-300 font-bold">+ Add Option</button>
          </div>
        )}

        {/* Helper Text for other types */}
        {type === "rating" && (
            <div className="p-4 bg-slate-800/50 border border-slate-700/50 rounded-lg text-center text-slate-400 text-sm mb-4">
                Participants will vote using a 5-star scale.
            </div>
        )}

        {type === "word_cloud" && (
            <div className="p-4 bg-slate-800/50 border border-slate-700/50 rounded-lg text-center text-slate-400 text-sm mb-4">
                Participants will submit words to build a cloud.
            </div>
        )}

        {type === "open_ended" && (
            <div className="p-4 bg-slate-800/50 border border-slate-700/50 rounded-lg text-center text-slate-400 text-sm mb-4">
                Participants will share thoughts that float up the screen as bubbles.
            </div>
        )}

        <div className="flex gap-3 mt-6">
          <button onClick={onClose} className="flex-1 py-3 text-slate-400 hover:text-white hover:bg-slate-800 rounded-lg transition">Cancel</button>
          <button 
            onClick={handleSubmit} 
            disabled={loading || !question.trim()} 
            className="flex-1 py-3 bg-linear-to-r from-blue-600 to-purple-600 hover:from-blue-500 hover:to-purple-500 text-white font-bold rounded-lg shadow-lg disabled:opacity-50 disabled:cursor-not-allowed transition-all transform active:scale-95"
          >
            {loading ? "Launching..." : "Launch üöÄ"}
          </button>
        </div>
      </div>
    </div>
  );
}