import { create } from 'zustand';

// --- DATA MODELS ---

export interface Question {
  id: string;
  text: string;
  votes: number;
  visible: boolean; // Used for Q&A Moderation
}

export interface User {
  id: number;
  email: string;
}

export interface Poll {
  type: "multiple_choice" | "rating" | "word_cloud" | "open_ended"; 
  question: string;
  is_open: boolean;
  options?: { label: string; votes: number }[];
  average?: number;
  counts?: Record<string, number>;
  words?: Record<string, number>;
  responses?: string[]; // Used for Transient Thoughts (open_ended)
}

export interface QuizQuestion {
  id: string;
  text: string;
  options: string[];
  correct_index: number;
  time_limit: number;
}

export interface QuizState {
  title: string;
  questions: QuizQuestion[];
  current_index: number;
  state: "LOBBY" | "QUESTION" | "LEADERBOARD" | "END";
  start_time_epoch: number;
}

export interface UserLocation {
  name: string;
  lat: number;
  lng: number;
  city: string;
}

export interface Branding {
  logo_url: string | null;
  theme_color: string;
}

// --- STORE DEFINITION ---

interface SessionState {
  // Connection & Room Data
  isConnected: boolean;
  participants: string[];
  locations: UserLocation[];
  
  // Interactive Elements
  currentPoll: Poll | null;
  questions: Question[]; // Q&A
  lastReaction: string | null;
  
  // Gamification
  quiz: QuizState | null;
  quizScores: Record<string, number>; 
  
  // Customization
  branding: Branding;

  // Authentication
  token: string | null;
  user: User | null;

  // --- ACTIONS ---
  setConnectionStatus: (status: boolean) => void;
  setParticipants: (names: string[]) => void;
  setLocations: (locs: UserLocation[]) => void;
  
  setPoll: (poll: Poll) => void;
  setQuestions: (qs: Question[]) => void;
  triggerReaction: (emoji: string) => void;
  
  setQuiz: (quiz: QuizState | null, scores?: Record<string, number>) => void;
  setBranding: (branding: Branding) => void;
  
  setToken: (token: string | null) => void;
  setUser: (user: User | null) => void;
}

export const useSessionStore = create<SessionState>((set) => ({
  // --- INITIAL STATE ---
  isConnected: false,
  participants: [],
  locations: [],
  
  currentPoll: null,
  questions: [],
  lastReaction: null,
  
  quiz: null,
  quizScores: {},
  
  branding: {
    logo_url: null,
    theme_color: "#3b82f6", // Default blue-500
  },

  // Check localStorage for token on load (Client-side only check)
  token: typeof window !== 'undefined' ? localStorage.getItem('token') : null,
  user: null,

  // --- ACTIONS IMPLEMENTATION ---

  setConnectionStatus: (status) => set({ isConnected: status }),
  setParticipants: (names) => set({ participants: names }),
  setLocations: (locs) => set({ locations: locs }),

  setPoll: (poll) => set({ currentPoll: poll }),
  setQuestions: (qs) => set({ questions: qs }),
  triggerReaction: (emoji) => set({ lastReaction: `${emoji}-${Date.now()}` }),

  setQuiz: (quiz, scores) => set((state) => ({ 
      quiz, 
      quizScores: scores || state.quizScores 
  })),

  setBranding: (branding) => set({ branding }),

  // Auth Actions with Persistence
  setToken: (token) => {
    if (typeof window !== 'undefined') {
      if (token) localStorage.setItem('token', token);
      else localStorage.removeItem('token');
    }
    set({ token });
  },
  setUser: (user) => set({ user }),
}));