"use client";
import { useEffect, useState } from "react";

export default function TransientThoughts({ responses }: { responses: string[] }) {
  const [bubbles, setBubbles] = useState<{ id: number; text: string; left: number; speed: number }[]>([]);

  // When a new response arrives (it's at index 0), add a bubble
  useEffect(() => {
    if (responses.length === 0) return;
    
    const newest = responses[0];
    const newBubble = {
      id: Date.now(),
      text: newest,
      left: Math.random() * 80 + 10, // Random position 10% - 90%
      speed: Math.random() * 5 + 5   // Random speed 5s - 10s
    };

    setBubbles(prev => [...prev, newBubble]);

  }, [responses]); // Trigger whenever responses change

  return (
    <div className="relative w-full h-100 overflow-hidden bg-slate-900/50 rounded-xl border border-slate-700">
      {/* Floating Bubbles */}
      {bubbles.map((b) => (
        <div
          key={b.id}
          className="absolute bottom-0 px-6 py-3 bg-linear-to-r from-blue-600 to-purple-600 rounded-full text-white font-bold shadow-lg animate-floatUp opacity-0"
          style={{
            left: `${b.left}%`,
            animationDuration: `${b.speed}s`,
            animationFillMode: 'forwards' 
          }}
        >
          {b.text}
        </div>
      ))}
      
      {/* Placeholder if empty */}
      {bubbles.length === 0 && (
        <div className="absolute inset-0 flex items-center justify-center text-slate-500">
          Waiting for thoughts...
        </div>
      )}
      
      <style jsx>{`
        @keyframes floatUp {
          0% { transform: translateY(100%) scale(0.5); opacity: 0; }
          10% { opacity: 1; transform: translateY(0) scale(1); }
          90% { opacity: 1; }
          100% { transform: translateY(-500px) scale(1); opacity: 0; }
        }
        .animate-floatUp {
          animation-name: floatUp;
          animation-timing-function: linear;
        }
      `}</style>
    </div>
  );
}